import React from 'react';
import Link from 'gatsby-link';
import logo from './logo.png';


const Footer = () =>
  <footer style={{ margin: '0 auto', width: '90%'}}>
    <img style={{float: 'right'}} src={logo} alt="Logo" /> 
  </footer>

export default Footer



